import React, { PureComponent } from 'react';
import Query from './Query';
import Mutation from './Mutation';

class TableWrapper extends PureComponent {
    state = {
        open: false,
    };
    componentWillMount() {
        this.setState({
            reset: true
        });
    }
    render() {
        const {tableMutation, tableQuery, dataQuery, defaultState, getQueryVariables} = this.props;
        return (
            <Mutation mutation={tableMutation}>
                {(lmutate, args) => {
                    if (this.state.reset) {
                        lmutate({ variables: defaultState });
                        this.setState({ reset: false });
                    }
                    return (
                        <Query query={tableQuery}>
                            {({ data }) => {
                                const tableSettings = data[Object.keys(data)[0]];
                                const { page, rowsPerPage } = tableSettings;
                                return (
                                    <Query query={dataQuery} variables={{ limit: rowsPerPage, skip: page * rowsPerPage, ...getQueryVariables() }}>
                                    {(props) => {
                                        return this.props.children(tableSettings, lmutate, props);
                                    }}
                                    </Query>
                                );
                            }}
                        </Query>
                    )
                }}
            </Mutation>
        );
    }
}

export default TableWrapper;


