import React from 'react';
import TableCell from '@material-ui/core/TableCell';
export default ({match, bgcolor = "rgba(0, 255, 0, 0.5)", renderValue = (_, val) => val}) => (key = null, items, colorValue) => {
    if (!(items instanceof Object)) {
        return (
            <TableCell style={{ color: colorValue, backgroundColor: key === match ? bgcolor : null }} key={key || Math.random()}>{renderValue(key, items)}</TableCell>
        );
    }
    const { id, __typename, paid, ...rest } = items;
    return (
        Object.keys(rest).map(e => (
            <TableCell style={{ color: colorValue, backgroundColor: paid ? bgcolor : null }} key={key ? (key + id + e) : (id + e)}>{renderValue(e, rest[e])}</TableCell>
        ))
    );
}


